import React from "react";
import { Link } from "gatsby";

const PostItem = ({ post, title }) => {
  return (
    <div key={post.fields.slug} className="post__grid__item">
      <Link to={post.fields.slug} itemProp="url" className="post__tile">
        <article itemScope itemType="http://schema.org/Article">
          <header>
            <h2 className="post__tile__headline">
              <span itemProp="headline">{title}</span>
            </h2>
            <section>
              <p
                dangerouslySetInnerHTML={{
                  __html: post.frontmatter.description || post.excerpt,
                }}
                itemProp="description"
                className="post__tile__description"
              />
            </section>
          </header>
          <small className="post__tile__date">{post.frontmatter.date}</small>
        </article>
      </Link>
    </div>
  );
};

export default PostItem;
