import React from "react";
import { graphql } from "gatsby";

import Layout from "../components/layout";
import SEO from "../components/seo";
import PostItem from "../components/post-item";

const TagPosts = ({ data, location }) => {
  const posts = data.allMarkdownRemark.nodes;
  return (
    <Layout location={location} title="zainsci-blog">
      <SEO
        title={
          location.pathname.replace("/tag/", "").toUpperCase() +
          " related posts"
        }
      />
      <div>
        <ol style={{ listStyle: `none` }} className="post__grid">
          {posts.map(post => {
            return <PostItem post={post} title={post.frontmatter.title} />;
          })}
        </ol>
      </div>
    </Layout>
  );
};

export default TagPosts;

export const pageQuery = graphql`
  query MyQuery($tag: String) {
    allMarkdownRemark(
      sort: { fields: frontmatter___date, order: DESC }
      filter: { frontmatter: { tags: { eq: $tag } } }
    ) {
      nodes {
        excerpt
        fields {
          slug
        }
        frontmatter {
          date(formatString: "MMMM DD, YYYY")
          title
        }
      }
    }
  }
`;
